
import tabGroup from '@/components/group/tab'
import Toast from 'vux/src/components/toast/index.vue'

export default{
components: {
tabGroup: tabGroup,
Toast

},
mounted(){

},
data(){
return {
title:"选择店铺",
rightTitle:'完成',
selected:false
}
},
methods:{
finish(){
if(this.selected==true){
this.$store.commit('setdistributionMode',false)
this.$router.go(-1);
}else{
this.$vux.toast.text('请选择一家店铺')
}
},
select(){
this.selected=!this.selected
}

}

}



